/*
* Column: bind clone events
*
* @since 2.0
*/
import { COLUMN_EVENTS } from "../column";
export var initClone = function (column) {
    column.getElement().querySelectorAll('.clone-button').forEach(function (element) {
        element.addEventListener('click', function (e) {
            e.preventDefault();
            if (!column.isOriginal()) {
                column.events.emit(COLUMN_EVENTS.CLONE);
            }
        });
    });
};
